import React from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'gatsby'
import CardGrid from '../components/card-grid'

import Layout from '../components/layout'
import { MatterPreview } from '../components/matter-preview'
import BreadcrumbNav from '../components/breadcrumb-nav'
import MyJumbotron from '../components/jumbotron'

import { flattenFileNode, classeNameFromClasse } from '../helpers'

const Classe = ({ pageContext, data, location }) => {
  const { classe } = pageContext

  // aplat des objets
  let nodes = data.allSitePage.edges.map(flattenFileNode)
  // on filtre ici
  // TODO filtrer dans la requête graphql
  nodes = nodes.filter((node) => node.classe === classe)

  const totalCount = nodes.length
  const classeHeader = `${totalCount} matière${
    totalCount === 1 ? `` : `s`
  } en ${classeNameFromClasse(classe)}`

  return (
    <Layout location={location} data={data}>
      <BreadcrumbNav node={pageContext} />
      <MyJumbotron>
        <h1> {classeHeader} </h1>
      </MyJumbotron>
      <CardGrid>
        {nodes.map((node) => (
          <MatterPreview key={node.id} node={node} dataShowMatter />
        ))}
      </CardGrid>
    </Layout>
  )
}

Classe.propTypes = {
  pageContext: PropTypes.shape({
    classe: PropTypes.string.isRequired,
  }),
  data: PropTypes.shape({
    allSitePage: PropTypes.shape({
      edges: PropTypes.arrayOf(
        PropTypes.shape({
          node: PropTypes.shape({
            id: PropTypes.string.isRequired,
            childMarkdownRemark: PropTypes.shape({
              fields: PropTypes.shape({
                classe: PropTypes.string.isRequired,
                chapter: PropTypes.string.isRequired,
                part: PropTypes.string.isRequired,
                slug: PropTypes.string.isRequired,
              }),
            }),
          }),
        })
      ),
    }),
  }),
  location: PropTypes.string.isRequired,
}

export default Classe

export const query = graphql`
  query ClassePage {
    allSitePage(
      filter: { component: { glob: "**/matter.js" } }
      sort: { fields: [path] }
    ) {
      edges {
        node {
          id
          path
          pageContext
        }
      }
    }
  }
`
